(function() {
  'use strict';

  angular
    .module('dowser')
    .factory('AmChartParser', AmChartParserService);

  /** @ngInject */
  function AmChartParserService($filter, AmChartFormats) {
    function AmChartParser(data) {
      this.results = {};
      this.today = moment().startOf('day');
      this.__dates = [];
      this.__unparsedData = data;
    }

    AmChartParser.prototype.__createResultDates = function() {
      var self = this;
      self.__unparsedData.forEach(function(category) {
        category.data.forEach(function(data) {
          self.__dates.push(data.date);
        });
      });
      var maxDate = moment(Math.max.apply(null, self.__dates));
      var minDate = moment(Math.min.apply(null, self.__dates));
      moment.range(minDate, maxDate).by('hours', function(date) {
        self.results[date.format('YYYY-MM-DD')] = { date: date };
      });
    };

    AmChartParser.prototype.__convertDate = function(date) {
      return moment(date).startOf('day');
    };

    AmChartParser.prototype.__convertAllDates = function() {
      var self = this;

      this.__unparsedData.forEach(function(category) {
        category.data.forEach(function(data) {
          data.date = self.__convertDate(data.date);
        });
      });
    };

    AmChartParser.prototype.__formatResultDates = function() {
      this.results.forEach(function(result) {
        result.date = result.date.format('YYYY-MM-DD');
      });
    };

    AmChartParser.prototype.__getResultIndexByDate = function(date) {
      for (var i = 0; i < this.results.length; i += 1) {
        if (this.results[i].date.isSame(date)) {
          return i;
        }
      }
    };

    AmChartParser.prototype.__buildResults = function() {
      var self = this;

      self.__unparsedData.forEach(function(set) {
        set.data.forEach(function(data) {
          var date = data.date.format('YYYY-MM-DD');

          if (self.results[date][set.category]) {
            self.results[date][set.category] += data.value;
          } else {
            self.results[date][set.category] = data.value;
          }

          if (data.date.diff(self.today) >= 0) {
            self.results[date].dashLength = AmChartFormats.dashLength;
          }
        });
      });
    };

    AmChartParser.prototype.__convertResultsToArray = function() {
      var self = this;
      self.results = Object.keys(self.results).map(function(key) {
        return self.results[key];
      });
    };

    AmChartParser.prototype.parse = function() {
      var self = this;

      self.__createResultDates();
      self.__convertAllDates();
      self.__buildResults();
      self.__convertResultsToArray();
      self.__formatResultDates();

      return self.results;
    };

    return AmChartParser;
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .factory('AmChartOptions', AmChartOptionsFactory);

  /** @ngInject */
  function AmChartOptionsFactory(ChartDataMock, AmChartSerial, AmChartValueAxe, AmChartGraph, AmChartParser) {
    function AmChartOptions(identifier, datas) {
      var chart = new AmChartSerial();

      chart.valueAxes = [];
      chart.graphs = [];

      datas.forEach(function(data, index) {
        chart.valueAxes.push(new AmChartValueAxe(index));
        chart.graphs.push(new AmChartGraph(data, index));
      });

      chart.dataProvider = new AmChartParser(datas).parse();

      return chart;
    }

    return AmChartOptions;
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .factory('AmChartMiniOptions', AmChartMiniOptionsService);

  /** @ngInject */
  function AmChartMiniOptionsService(ChartDataMock, AmChartSerial, AmChartValueAxe, AmChartGraph, AmChartParser, AmChartFormats) {
    function AmChartMiniOptions(identifier, data) {
      var chart = new AmChartSerial();

      chart.valueAxes = [];
      chart.graphs = [];

      chart.valueAxes.push(new AmChartValueAxe(1));
      chart.graphs.push(new AmChartGraph(data, 1));

      delete chart.legend;
      delete chart.chartCursor;

      chart.colors = AmChartFormats.linesColorPallet;
      chart.categoryAxis.labelsEnabled = false;
      chart.categoryAxis.gridThickness = 0;
      chart.categoryAxis.dashLength = 0;
      chart.categoryAxis.gridAlpha = 0;
      chart.categoryAxis.axisAlpha = 0;
      chart.balloon = {
        borderAlpha: 0,
        borderThickness: 0,
        fillAlpha: 0,
        fontSize: 0
      }

      chart.graphs[0].bulletSize = 9;
      chart.graphs[0].lineThickeness = 2;

      if (!angular.isArray(data)) {
        data = [data];
      }

      chart.dataProvider = new AmChartParser([data[0]]).parse();

      return chart;
    }

    return AmChartMiniOptions;
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .factory('AmChartMerge', AmChartMergeService);

  /** @ngInject */
  function AmChartMergeService(Api) {
    function AmChartMerge(dataStore) {
      this.__realData = dataStore[0];
      this.__realDataCategory = this.__realData.category;
      this.__realDataUnit = this.__realData.unit;
      this.__mergeWith = dataStore.slice(1);
    }

    AmChartMerge.prototype.__destructureData = function(structuredData) {
      structuredData.data.sort(function(left, right) {
        return left.date.valueOf() - right.date.valueOf();
      });
      var dates = structuredData.data.map(function(d) {
        return d.date;
      });
      var values = structuredData.data.map(function(d) {
        return d.value;
      });
      return {
        dates: dates,
        values: values
      };
    };

    AmChartMerge.prototype.__structureData = function(destructuredData) {
      var data = {
        dates: this.__realData.dates,
        values: destructuredData
      };

      var merged = {
        category: 'Merged ' + this.__realDataCategory,
        unit: this.__realDataUnit,
        data: []
      };

      for (var i = 0; i < data.dates.length; i += 1) {
        merged.data[i] = { date: data.dates[i], value: data.values[i] };
      }

      return merged;
    };

    AmChartMerge.prototype.__removeBeforeToday = function(d) {
      var today = moment().startOf('day');
      var n = { dates: [], values: [] };

      for (var i = 0; i < d.dates.length; i += 1) {
        if (d.dates[i].diff(today) >= 0) {
          n.dates.push(d.dates[i]);
          n.values.push(d.values[i]);
        }
      }

      return n;
    }

    AmChartMerge.prototype.get = function(cb) {
      var self = this;

      if (self.__mergeWith.length < 1) {
        return;
      }

      self.__realData = self.__destructureData(self.__realData);
      self.__mergeWith = self.__mergeWith.map(function(d) {
        return self.__destructureData(d);
      });

      self.__realData = self.__removeBeforeToday(this.__realData);
      self.__mergeWith = self.__mergeWith.map(function(d) {
        return self.__removeBeforeToday(d);
      });

      var realData = self.__realData.values;
      var mergeWith = self.__mergeWith.map(function(d) {
        return d.values;
      });

      Api
        .merge({ realData: realData, mergeWith: mergeWith || [] })
        .then(function(response) {
          var data = response.data.map(function(v) { return Math.round(v) });
          data = self.__structureData(data);
          cb(data);
        });

      return self.dataStore;
    };

    return AmChartMerge;
  }
})();

