(function() {
  'use strict';

  angular
  .module('dowser')
  .factory('AmChartDataStore', AmChartDataStore);

  /** @ngInject */
  function AmChartDataStore() {
    function AmChartDataStore(data) {
      if (!data) {
        data = [];
      }

      if (!angular.isArray(data)) {
        data = [data];
      }

      this.data = data;
    }

    AmChartDataStore.prototype.get = function() {
      return this.data;
    };

    AmChartDataStore.prototype.__toggle = function(data) {
      var index;
      this.data.every(function(d, i) {
        return d.id !== data.id || (function() { index = i; })();
      });

      if (index) {
        this.data.splice(index, 1);
      } else {
        this.data.push(data);
      }
    };

    AmChartDataStore.prototype.toggle = function(data) {
      if (!angular.isArray(data)) {
        this.__toggle(data);
      } else {
        data.forEach(function(d) { this.__toggle(d); });
      }
    };

    AmChartDataStore.prototype.addOnFirst = function(data) {
      this.data.unshift(data);
    };

    return AmChartDataStore;
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .service('User', User);

  /** @ngInject */
  function User($q, $log, Api) {
    var self = this;

    self.id = '007';
    self.username = 'james';
    self.myData = [];
    self.notMyData = [];

    self.loadData = function() {
      return new $q(function(resolve) {
        var promises = []
        promises.push(Api.get('DataType/GetByUser/' + self.username));
        promises.push(Api.get('DataType/GetByNotUser/' + self.username));

        $q.all(promises)
          .then(function(responses) {
            self.myData = responses[0].data;
            self.notMyData = responses[1].data.shuffle().slice(0, 6);

            resolve();
          })
          .catch($log.error);
      });
    }
  }
})();

