(function() {
  'use strict';

  angular
    .module('dowser')
    .factory('DataGenerator', DataGenerator);

  /** @ngInject */
  function DataGenerator() {
    var days = 30;

    return {
      generate: generate
    };

    function generate() {
      var data = [];

      for (var i = 0; i < days; i++) {
        var item = createItem(i);
        data.push(item);
      }

      return data;
    }

    function createItem(i) {
      var item = {};
      item.date = createTimeStamp(i);
      item.value = randomNumber(1, 100);
      return item;
    }

    function createTimeStamp(i) {
      var today = moment();
      var startDate = today.clone().subtract(days - i, 'days');
      return +moment(startDate);
    }

    function randomNumber(start, end) {
      return Math.floor((Math.random() * end) + start);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .factory('ChartData', ChartData);

  /** @ngInject */
  function ChartData($rootScope, Api) {
    return {
      load: load,
      loadOn: loadOn
    };

    function parseData(data, identifier) {
      return {
        id: identifier,
        category: data.name,
        unit: data.kind,
        data: data.points.map(function(point) {
          return {
            date: point.date,
            value: Math.round(Number(point.value))
          };
        })
      };
    }

    function load(identifier) {
      Api.get('DataType/Get/' + identifier)
        .then(function(response) {
          var data = parseData(response.data, identifier);
          $rootScope.$emit('chart:' + identifier + ':load', data);
        });
    }

    function loadOn(containerIdentifier, identifier) {
      Api.get('DataType/Get/' + identifier)
        .then(function(response) {
          var data = parseData(response.data, identifier);
          $rootScope.$emit('chart:' + containerIdentifier + ':load', data);
        });
    }
  }
})();

