(function() {
  'use strict';

  angular
    .module('dowser')
    .factory('Api', Api);

  /** @ngInject */
  function Api($timeout, $http, $log) {

    var url = 'http://158.85.206.13:8081/';
    var urlMerge = 'http://158.85.199.5:8002';

    return {
      __get: __get,
      get: get,
      post: post,
      merge: merge
    };

    function __get(endpoint) {
      return new Promise(function(resolve) {
        $log.info('Getting: ' + url + endpoint);
        $timeout(resolve, 2000);
      });
    }

    function get(endpoint) {
      return $http.get(url + endpoint);
    }

    function post(endpoint, data) {
      return $http.post(url + endpoint, data);
    }

    function merge(data) {
      return $http.post(urlMerge, data);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .directive('chart', chart);

  /** @ngInject */
  function chart() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/chart/chart.html',
      scope: {
        identifier: '@'
      },
      controller: ChartController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function ChartController($scope, $rootScope, $timeout, AmChartOptions, AmChartDataStore, AmChartMerge) {
      var vm = this;

      vm.chartId = 'chart-' + vm.identifier
      vm.dataStore = new AmChartDataStore();

      vm.renderChart = function() {
        if (AmCharts.isReady) {
          var chart = new AmChartOptions(vm.identifier, vm.dataStore.get());
          return AmCharts.makeChart(vm.chartId, chart);
        }
        AmCharts.ready(vm.renderChart);
      };

      vm.loadEvent = $rootScope.$on('chart:' + vm.identifier + ':load', function(evt, data) {
        vm.dataStore.toggle(data);
        $timeout(vm.renderChart);
      });

      vm.mergeEvent = $rootScope.$on('chart:' + vm.identifier + ':merge', function() {
        var data = angular.copy(vm.dataStore.get());
        new AmChartMerge(data).get(function(merged) {
          vm.dataStore.addOnFirst(merged);
          $timeout(vm.renderChart);
        });
      });
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .directive('chartMini', chartMini);

  /** @ngInject */
  function chartMini() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/chart/chart-mini.html',
      scope: {
        identifier: '@'
      },
      controller: ChartMiniController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function ChartMiniController($rootScope, $timeout, AmChartMiniOptions) {
      var vm = this;

      vm.chartId = 'chart-' + vm.identifier

      vm.renderChart = function(data) {
        if (AmCharts.isReady) {
          var chart = new AmChartMiniOptions(vm.identifier, data);
          return AmCharts.makeChart(vm.chartId, chart);
        }
        AmCharts.ready(vm.renderChart);
      };

      vm.event = $rootScope.$on('chart:' + vm.identifier + ':load', function(evt, data) {
        $timeout(function() { vm.renderChart(data); });
      });
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .controller('WizardController', WizardController);

  /** @ngInject */
  function WizardController($rootScope, $state) {
    var vm = this;

    vm.progress = false;
    vm.progressClass = 'progress-hide';

    vm.event = $rootScope.$on('wizard:progress', function() {
      vm.progress = !vm.progress;
      vm.progressClass = vm.progress ? 'progress-show' : 'progress-hide';
    });

    $state.go('wizard.register');
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .controller('RegisterController', RegisterController);

  /** @ngInject */
  function RegisterController($rootScope, $timeout, $state, User) {
    var vm = this;

    vm.data = {
      name: undefined,
      kind: undefined,
      userId: User.username
    };

    vm.next = function() {
      $rootScope.$emit('wizard:progress');
      $timeout(registerData, 500);
    };

    function registerData() {
      $rootScope.$emit('wizard:progress');
      $state.go('wizard.importer', vm.data);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($state, $timeout, Particles) {
    var vm = this;

    Particles.start('particles-js');

    vm.getStarted = function() {
      $timeout(function () {
        $state.go('wizard.register');
      }, 500);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .controller('ImporterController', ImporterController);

  ImporterController.$inject = ['$rootScope', '$timeout', '$state', '$stateParams', 'Api', 'DataGenerator'];

  /** @ngInject */
  function ImporterController($rootScope, $timeout, $state, $stateParams, Api, DataGenerator) {
    var vm = this;
    var comment = '// This is just an Example data\n';
    var generatedData = DataGenerator.generate();

    vm.data = {
      points: addPrefixComment(generatedData)
    };

    vm.aceSettings = {
      showGutter: false,
      theme:'twilight',
      mode: 'json',
      firstLineNumber: 5
    };

    vm.next = function() {
      $rootScope.$emit('wizard:progress');
      $timeout(importerData, 500);
    };

    function importerData() {
      var data = $stateParams;
      data.points = removePrefixComment(vm.data.points);
      $rootScope.$emit('wizard:progress');
      $state.go('wizard.agreement', { data: data });
    }

    function addPrefixComment(data) {
      var stringfyData = angular.toJson(data, 2);
      return comment.concat(stringfyData);
    }

    function removePrefixComment(stringfyData) {
      var data = stringfyData.replace(comment, '');
      return angular.fromJson(data);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .controller('DashboardController', DashboardController);

  /** @ngInject */
  function DashboardController($timeout, $state, ChartData, User, $rootScope) {
    var vm = this;

    vm.inputSearch = '';
    vm.mainChart = {};
    vm.chartList = [];
    vm.comparableCharts = [];

    vm.loadMyChart = function() {
      ChartData.load(vm.mainChart.id);
    };

    vm.loadComparableCharts = function() {
      vm.comparableCharts.forEach(function(comparableChartsItem, index) {
        $timeout(function() {
          ChartData.load(comparableChartsItem.id);
        }, 3000 * index);
      });
    };

    vm.addNewChart = function() {
      $timeout(function () {
        $state.go('wizard.register');
      }, 500);
    };

    vm.setMainChart = function(chart) {
      vm.mainChart = false;
      $timeout(function() {
        vm.mainChart = chart;
        vm.loadMyChart();
      }, 500);
    };

    vm.createMerge = function() {
      $rootScope.$emit('chart:' + vm.mainChart.id + ':merge');
    };

    vm.addChart = function(chart) {
      ChartData.loadOn(vm.mainChart.id, chart.id);
    };

    vm.initialize = function() {
      User.loadData().then(function() {
        vm.chartList = User.myData;
        vm.comparableCharts = User.notMyData;
        vm.mainChart = vm.chartList[0];
        vm.loadMyChart();
        vm.loadComparableCharts();
      });
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .controller('AgreementController', AgreementController);

  /** @ngInject */
  function AgreementController($rootScope, $state, $timeout, $stateParams, $log, Api) {
    var vm = this;

    vm.accept = debounce(function() {
      Api.post('DataType/Insert', $stateParams.data)
        .then(function() {
          $rootScope.$emit('wizard:progress');
          $state.go('dashboard');
        })
        .catch($log.error);
    });

    vm.deny = debounce(function() {
      $rootScope.$emit('wizard:progress');
      $state.go('main');
    });

    function debounce(callback) {
      return function() {
        $rootScope.$emit('wizard:progress');
        $timeout(callback, 500);
      };
    }
  }
})();

